@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans&display=swap);
.main-container {
  font-family: "Montserrat", sans-serif;
  background-color: whitesmoke;
}

.main-container h1 h2 h3 {
  font-family: "Quicksand", sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.StaticHeader_header__2ca6z {
	margin-top: 50px;
	font-size: calc(10px + 1.2vmin);
}

.About_about_container__1MTzJ {
	display: grid;
	padding: 3vh 0;
	grid-template-columns: 2fr 3fr 3fr 2fr;
	grid-template-rows: auto;
}

.About_leftcol__aVmmX > * {
	margin: 20px 0;
}

.About_leftcol__aVmmX {
	grid-column: 2/3;
}
.About_rightcol__264y6 > * {
	margin: 20px;
}

.About_rightcol__264y6 {
	grid-column: 3/4;
}

.nav-container {
	display: grid;
	place-items: center;
	width: 10vw;
	padding: auto;
}

.nav-list {
	list-style-type: none;
	color: black;
}

li a,
li a:visited {
	color: black;
	transition: all 0.6s ease-in-out;
}

.nav-list > * {
	padding-bottom: 5vh;
	transition: all 0.75s ease-in-out;
}

.nav-list > *:hover {
	-webkit-transform: scale(1.15);
	        transform: scale(1.15);
	color: rgb(0, 177, 168);
}

.nav-list a:hover {
	color: rgb(0, 177, 168);
}

.color {
	display: grid;
	place-items: center;
	height: 1000px;
	background-color: whitesmoke;
}
.App-header {
	font-size: calc(30px + 2vmin);
	font-weight: lighter;
	font-family: "DM sans", sans-serif;
	color: white;

	background: rgb(0, 62, 173);
	background: linear-gradient(
		175deg,
		rgba(0, 62, 173, 1) 0%,
		rgba(26, 132, 205, 1) 36%,
		rgba(0, 212, 255, 1) 54%,
		rgba(139, 255, 209, 1) 70%,
		#f5f5f5 84%
	);
}

.about_dropdown {
	text-align: center;
}

.App-link {
	color: #61dafb;
}

button {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.chev-down {
	margin-top: 2vh;
}

.chev-down:hover {
	color: grey;
}

.profile-pic {
	width: 26vw;
	margin: 10vh;
	border-radius: 50%;
}

h1 {
  text-align: center;
}

.ProjectsTour_project_card__2oy3F {
  grid-column: 2/3;
  margin-right: 20vw;
  text-align: left;
  width: 25vw;
  height: 15vw;
}
.ProjectsTour_project_card__2oy3F a {
  text-decoration: none;
}

.ProjectsTour_project_desc__2zyaX {
  text-align: right;
  padding: 1vw 0;
}

.ProjectsTour_sticky__UiSTq {
  margin-left: 15%;
}

.experience-card {
	grid-column: 2/3;
	margin-right: 15vw;
	text-align: left;
	width: 25vw;
	height: 15vw;
}
.experience-card a {
	text-decoration: none;
}
.experience-card h3 {
	color: rgb(0, 144, 144);
}
.experience-desc {
	text-align: right;
	padding: 0.5vw 0;
}
.sticky {
	margin-left: 15%;
}

